* {
  letter-spacing: 0.035rem;
  font-family: 'Roboto', sans-serif;
  color: #404040;
  cursor: default;
  transition: all 0.25s !important;
}

a, i {
  color: #404040;
  cursor: pointer;
}

i {
  font-size: 1.5rem !important;
}

nav {
  position: fixed;
  width: 1366px;
}

nav .nav-left {
  text-align: left;
}

nav .nav-right {
  float: right;
}

nav ul {
  margin: 0%;
  width: 100%;
  height: 7.5vh;
  list-style: none;
  height: auto;
  box-sizing: border-box;
}

nav li {
  cursor: pointer;
  text-transform: uppercase;
  display: inline-block !important;
  padding: 3% 2% 0.75% 2%;
  border-bottom: transparent 2px solid;
}

nav li a {
  letter-spacing: 0.15rem;
}

nav li:hover a, nav li:hover i {
  color: #309c8d;
}

nav li:hover {
  border-color: #309c8d;
}

@media (min-width: 1024px) and (max-width: 1550px) {
  nav {
    width: 966px;
  }
}

.container {
  max-width: 1366px;
  margin: 0% auto 0% auto !important;
  box-sizing: border-box;
}

@media (min-width: 1024px) and (max-width: 1550px) {
  .container {
    max-width: 966px;
  }
}

.content {
  padding: 0%;
  margin-bottom: 0%;
}

.content span {
  margin-top: 5%;
  font-size: 1.5rem;
  color: gray;
}

.content section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.box-wrapper {
  width: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.about-box {
  width: 30%;
}

.about-box .about-img {
  width: 100%;
  height: 30vh;
  background-color: gray;
  margin-bottom: 7.5%;
}
